<style type="stylesheet/scss" lang="scss">
	@import "../../scss/variables";
	.page-user{
		.page-user-banner{
			padding: 0.25rem 0 0.25rem 0.3rem;
			background: url(../../assets/userBg.png) no-repeat;
			background-size: 100% 100%;
			.page-user-banner-sc{
				width: 0.5rem;
				height:0.5rem;
				margin-right: 0.2rem;
			}
			p{
				color:#fff;
				line-height: 0.25rem;
				font-size: $font-size-littlebig;
			}
		}
		.page-user-zjcxxx{
			background: #fff;
			margin-bottom: 0.6rem;
			.page-user-zjcx,.page-user-wdsc{
				li:nth-child(1){
					padding-left: 0.1rem;
					color:#108ee9;
					font-size: 0.16rem;
				}
				li{
					line-height: 0.4rem;
					border-bottom:1px solid #f5f5f5;
					font-size: $font-size-littlebig;
					padding: 0 0.2rem;
					.icon-jiantou{
						color: #ddd;
					}
				}
			}
		}
	}
</style>
<template>
	<div class="page-user">
		<div class="page-user-banner">
			<img class="page-user-banner-sc el-left" src="../../assets/userSs.png" />
			<div class="el-left">
				<p>
					{{userBox.fjmc}}
				</p>
				<p>
					{{userBox.zc}}：{{userBox.name}}
				</p>
			</div>
			<div style="clear: both"></div>
		</div>
		<div class="page-user-zjcxxx">
			<ul class="page-user-zjcx">
				<li><i class="iconfont icon-danjuchaxun"></i>&nbsp;最近查询</li>
				<li v-for="(item, index) in zjcxBox" :key="index">
					<span class="el-left">{{item.name}}</span> <i class="iconfont icon-jiantou el-right"></i>
          <div style="clear: both"></div>
				</li>
			</ul>
			<ul class="page-user-wdsc">
				<li class="czy-list-title"><i class="iconfont icon-wenjianjia"></i>&nbsp;我的收藏</li>
				<li v-for="(item, index) in wdscBox" :key="index">
					<span class="el-left">{{item.name}}</span> <i class="iconfont icon-jiantou el-right"></i>
					<div style="clear: both"></div>
				</li>
			</ul>
		</div>

	</div>
</template>
<script>
import {animation} from '../commons/baseVue';
export default{
  name: 'czy-user',
  data () {
    return {
      userBox: [],
      zjcxBox: [],
      wdscBox: []
    }
  },
  mixins: [animation],
  created () {
    this.getData();
  },
  methods: {
    getData () {
      //      this.$http.get('../../../../static/data/user.json')
      //        .then((res) => {
      //          this.userBox = res.data;
      //          this.zjcxBox = this.userBox.zjcxList;
      //          this.wdscBox = this.userBox.wdscList;
      //        })
      this.userBox = {
        'zjcxList': [
          {
            'name': '杭州孚嘉科技'
          },
          {
            'name': '杭州天堂'
          },
          {
            'name': '杭州阿里巴巴'
          },
          {
            'name': '杭州网易'
          },
          {
            'name': '杭州网易'
          },
          {
            'name': '杭州网易'
          },
          {
            'name': '杭州网易'
          },
          {
            'name': '杭州网易'
          },
          {
            'name': '杭州网易111'
          }
        ],
        'wdscList': [
          {
            'name': '杭州网易'
          },
          {
            'name': '杭州网易111'
          },
          {
            'name': '杭州网易22'
          }
        ],
        'fjmc': '杭州市拱墅分局',
        'zc': '局长',
        'name': '李四'
      };
      this.zjcxBox = this.userBox.zjcxList;
      this.wdscBox = this.userBox.wdscList;
    }
  }
}
</script>



